<template>
	<div id="app">

		<div id="film-info">
			<b-form-input v-model="titleToSearch" type="text" placeholder="Enter film name"></b-form-input>

			<i class="fa fa-spinner fa-spin" v-if="loading"></i>
			<template v-else>
				<i class="fa fa-search" v-show="isEmpty"></i>
				<i class="fa fa-times" v-show="isDirty" @click="reset"></i>
			</template>

			<!-- the input field -->
			<input type="text"
				   placeholder="Enter filter name"
				   autocomplete="off"
				   v-model="query"
				   @keydown.down="down"
				   @keydown.up="up"
				   @keydown.enter="hit"
				   @keydown.esc="reset"
				   @blur="reset"
				   @input="update"/>

			<!-- the list -->
			<ul v-show="hasItems">
				<!-- for vue@1.0 use: ($item, item) -->
				<li v-for="(item, $item) in items" :class="activeClass($item)" @mousedown="hit"
					@mousemove="setActive($item)">
					<span v-text="item.name"></span>
				</li>
			</ul>

		</div>

		<div id="map-detail">
			<gmap-map :center="center" :zoom="10" style="width: 100%; height: 600px">
				<gmap-marker
						v-for="m in markers"
						:position="m.position"
						:clickable="true"
						:draggable="true"
						@click="center=m.position"
				></gmap-marker>
			</gmap-map>
		</div>

	</div>
</template>

<script>

  import * as VueGoogleMaps from 'vue2-google-maps'
  import axios from 'axios'
  import Vue from 'vue'
  import VueTypeahead from 'vue-typeahead'

  Vue.use(VueGoogleMaps, {
    load: {
      key: 'AIzaSyBNCLmXai1-W7259Uj2QWHWp1mXPbl-GpI'
    }
  })

  export default {
    extends: VueTypeahead,

    name: 'app',

    data: function () {
      return {
        titleToSearch: '',

        src: 'http://localhost:8080/films/like?name=Hours',
        data: '',
        limit: 5,
        minChars: 1,
        selectFirst: true,
        queryParamName: 'search',

        center: {lat: 37.7749295, lng: -122.4194155},

        markers: [{
          position: {lat: 37.792427, lng: -122.41061}
        }, {
          position: {lat: 37.81722, lng: -122.370705}
        }]
      }
    },

    components: {
      Map
    },

    methods: {

      search: function () {
        axios.get(`http://localhost:8080/location?id=1`)
            .then(response => {
              // JSON responses are automatically parsed.
              this.location = response.data.name
            })
            .catch(e => {
              this.errors.push(e)
            })
      },
      onHit (item) {
        console.log('On hit')
        console.log(item)
      },

      // The callback function which is triggered when the response data are received
      // (optional)
      prepareResponseData (data) {
        console.log('receive response')
        console.log(data)
        return data
      }
    }
  }
</script>

<style>
	body {
		background-color: rgba(229, 229, 229, 0.44) !important;
	}

	#film-info {
		width: 80%;
	}

	#app {
		font-family: 'Avenir', Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		text-align: center;
		color: #2c3e50;
		width: 100%;
		height: 90%;
	}

	#top-ban {
		height: 50px;
		background-color: rgba(138, 210, 255, 0.71);
		font-size: 2em;
		color: #22313a;
	}

	#top-ban-label {
		margin-left: 10px;
	}

	#map-detail {
		width: 80%;
		height: auto;
		float: right;
		margin-top: 10px;
		padding-right: 10px;
	}

	#film-info {
		width: 18%;
		float: left;
		margin-left: 10px;
		margin-top: 10px;
	}
</style>



